@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@200;300;400;600;700;800;900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lato:wght@100;300;400;700;900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Nunito+Sans:wght@200;300;400;800;900&display=swap);
.toolbar {
    background-color: #6a53e4;
    height: 100vh;
    width: 50px;
}

.toolbar-items-section {
    position: absolute;
    top: 30%;
}

.toolbar-item-img {
    margin-left: 7px;
    height: 40px; /* make same height as active indicator to avoid jumping around */
    width: 40px; /* make all images the same width */
    object-fit: none; /* but don't adjust the image size/aspect ratio just the frame size */
}

.toolbar-active-indicator {
    margin-right: -4px; /* counter the width of the indicator */
}

.toolbar-item {
    margin-bottom: 100px;
}

.splitview-detail-column {
    border-left: 1px lightgray solid;
}

.login-background {
    height: 100vh;
    background-image: url("/meeting-room-schedule-visualisation/static/media/Login background.c0ce601e.jpg");
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}

.login-header-text {
    margin-top: 95px;
    color: #393a6d;
    font-weight: 700;
}

.login-information-text {
    font-size: medium;
    margin-top: 25px;
    color: lightgray;
}

.login-button:before {
    content: url(/meeting-room-schedule-visualisation/static/media/Outlook.107882b1.svg);
    display: inline-block;
    width: 30px;
    left: 10px;
    top: 13px;
    position: absolute;
}

.login-button {
    border-radius: 0.5rem !important;
    position: relative;
    padding-top: 15px !important;
    padding-bottom: 15px !important;
    padding-left: 55px !important;
    margin-top: 35px;
    background-color: #6a53e4 !important;
    color: white !important;
    font-size: large !important;
}

.login-copyright-text {
    position: absolute;
    bottom: 20px;
    font-size: small;
    color: lightgray;
    left: 50%;
    -webkit-transform: translate(-50%, 0);
            transform: translate(-50%, 0);
}


.login-popover {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 500px;
    height: 500px;
    margin: -250px 0 0 -250px; /* apply negative top and left margins to truly center the element */
    background-color: white;
    border-radius: 25px;
}


html, body {
    /*font-family: 'Nunito', sans-serif;*/
    font-family: 'Nunito Sans', sans-serif;
    /*font-family: 'Lato', sans-serif;*/
}
